.start {
  padding-top: 200px;
}
@media (max-width: 960px) {
  .start {
    padding-top: 110px;
  }
}
@media (max-width: 760px) {
  .start {
    padding-top: 90px;
  }
}
@media (max-width: 425px) {
  .start {
    padding-top: 60px;
  }
}
.start__title {
  font-family: var(--font-family);
  display: block;
  position: relative;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.3;
  color: var(--light-color);
  padding-left: 125px;
}
@media (max-width: 960px) {
  .start__title {
    font-size: 32px;
    padding-left: 110px;
  }
}
@media (max-width: 760px) {
  .start__title {
    padding-left: 90px;
  }
}
@media (max-width: 425px) {
  .start__title {
    font-size: 28px;
    padding-left: 0;
    padding-top: 70px;
  }
}
.start__title::before {
  content: "";
  display: block;
  position: absolute;
  background: url(../img/icons/icon-heading.svg) no-repeat;
  background-size: cover;
  width: 95px;
  height: 95px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
@media (max-width: 960px) {
  .start__title::before {
    width: 80px;
    height: 80px;
  }
}
@media (max-width: 760px) {
  .start__title::before {
    width: 60px;
    height: 60px;
  }
}
@media (max-width: 425px) {
  .start__title::before {
    width: 50px;
    height: 50px;
    top: 0;
    transform: translateY(0);
  }
}
.start__desc {
  max-width: 411px;
  font-family: var(--font-family);
  font-weight: 400;
  color: var(--light-color-opacity);
  font-size: 16px;
  line-height: 1.2;
  margin-top: 50px;
}
@media (max-width: 960px) {
  .start__desc {
    margin-top: 40px;
  }
}
.start__box {
  margin-top: 80px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 760px) {
  .start__box {
    margin-top: 60px;
  }
}
.start__item {
  display: flex;
  flex-direction: column;
  width: 25%;
}
@media (max-width: 760px) {
  .start__item {
    display: block;
    width: 100%;
    position: relative;
    padding-left: 100px;
  }
}
.start__count {
  display: block;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 36px;
  line-height: 1.2;
  color: var(--light-color);
  margin-bottom: 30px;
  order: -1;
}
@media (max-width: 760px) {
  .start__count {
    font-size: 28px;
    margin-bottom: 20px;
  }
}
.start__progress {
  display: flex;
  align-items: center;
  height: 80px;
  margin-bottom: 35px;
}
@media (max-width: 760px) {
  .start__progress {
    position: absolute;
    left: 0;
    width: 80px;
    top: 0;
    bottom: 0;
    height: auto;
    justify-content: center;
    margin: 0;
  }
}
.start__progress::after {
  content: "";
  flex-grow: 1;
  background: linear-gradient(94.26deg, #c89afc 9.51%, #7c6afa 90.23%);
  height: 80px;
}
@media (max-width: 760px) {
  .start__progress::after {
    height: 100%;
    flex-grow: 0;
    width: 80px;
    background: linear-gradient(180deg, #c89afc 9.51%, #7c6afa 90.23%);
  }
}
.start__progress--second::after {
  height: 60px;
  transform: scale(-1);
}
@media (max-width: 760px) {
  .start__progress--second::after {
    height: 100%;
    width: 60px;
  }
}
.start__progress--third::after {
  height: 30px;
}
@media (max-width: 760px) {
  .start__progress--third::after {
    height: 100%;
    width: 30px;
  }
}
.start__progress--end::after {
  height: 10px;
  transform: scale(-1);
}
@media (max-width: 760px) {
  .start__progress--end::after {
    height: 100%;
    width: 10px;
  }
}
.start__text {
  font-family: var(--font-family);
  color: var(--light-color-opacity);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  padding-right: 18px;
  max-width: 480px;
}
@media (max-width: 960px) {
  .start__text {
    margin-right: 0;
    margin-bottom: 30px;
  }
}
@media (max-width: 760px) {
  .start__text {
    padding-right: 0;
    margin-bottom: 20px;
  }
}